import React, { useEffect, useState } from 'react';
import { TutorialStep } from '../types';
import styles from '../scss/components/Tutorial.module.scss';

interface TutorialProps {
  steps: TutorialStep[];
  currentStep: number;
  currentSubStep?: number; //  
  onNext: () => void;
  onPrev?: () => void; //  
  onSkip: () => void;
  onComplete: () => void;
  onSwitchToCore?: () => void; //       
  onSubStepComplete?: () => void; //    
  canProceed?: boolean; // validation  
}

export const Tutorial: React.FC<TutorialProps> = ({
  steps,
  currentStep,
  currentSubStep = 0,
  onNext,
  onPrev,
  onSkip,
  onComplete,
  onSwitchToCore,
  onSubStepComplete,
  canProceed = true
}) => {
  const [highlightPosition, setHighlightPosition] = useState<DOMRect | null>(null);
  const [subStepTargetPosition, setSubStepTargetPosition] = useState<DOMRect | null>(null);
  const step = steps[currentStep];
  const activeSubStep = step?.subSteps?.[currentSubStep];

  useEffect(() => {
    if (!step) return;

    //     
    if (step.targetElement) {
      const element = document.querySelector(step.targetElement);
      if (element) {
        const rect = element.getBoundingClientRect();
        setHighlightPosition(rect);
      }
    } else {
      setHighlightPosition(null);
    }
  }, [step]);

  //    
  useEffect(() => {
    if (!activeSubStep || !activeSubStep.targetElement) {
      setSubStepTargetPosition(null);
      return;
    }

    const updateSubStepPosition = () => {
      const element = document.querySelector(activeSubStep.targetElement!);
      if (element) {
        const rect = element.getBoundingClientRect();
        setSubStepTargetPosition(rect);
      } else {
        setSubStepTargetPosition(null);
      }
    };

    updateSubStepPosition();

    //        
    const interval = setInterval(updateSubStepPosition, 100);
    return () => clearInterval(interval);
  }, [activeSubStep]);

  if (!step) return null;

  const isLastStep = currentStep === steps.length - 1;

  //     ( dim   )
  const canvasSteps = ['canvas-intro', 'canvas-pan', 'canvas-zoom', 'add-memo', 'memo-drag', 'connections', 'disconnect-mode', 'categories', 'category-area', 'undo-redo', 'select-memo', 'core-quick-nav-use'];
  const isCanvasStep = canvasSteps.includes(step.id);

  //    (     -   )
  const interactiveCanvasSteps = ['canvas-pan', 'canvas-zoom', 'add-memo', 'memo-drag', 'connections', 'disconnect-mode', 'categories', 'category-area', 'undo-redo', 'select-memo'];
  const isInteractiveStep = interactiveCanvasSteps.includes(step.id);

  //     (dim  )
  const coreFeaturesSteps = ['core-intro', 'core-quick-nav', 'core-importance', 'core-importance-filter', 'core-search'];
  const isCoreFeatureStep = coreFeaturesSteps.includes(step.id);

  //          (   )
  const coreRightMiddleSteps = ['core-quick-nav'];
  const isCoreRightMiddle = coreRightMiddleSteps.includes(step.id);

  //        (  dim )
  const isCanvasTarget = step.targetElement === '[data-tutorial="canvas"]' && isCanvasStep;

  //    (   )
  const getTooltipStyle = (): React.CSSProperties => {
    const padding = 20;
    // 5 (core-importance-filter) 267px,  1/3  533px (400 * 1.333)
    const tooltipWidth = step.id === 'core-importance-filter' ? 267 : 533;

    //        (   )
    if (isCoreRightMiddle) {
      return {
        position: 'fixed',
        top: '50%',
        right: `${padding}px`,
        transform: 'translateY(-50%)',
        zIndex: 10002,
        maxWidth: `${tooltipWidth}px`
      };
    }

    //     
    if (isInteractiveStep) {
      return {
        position: 'fixed',
        top: `${padding}px`,
        right: `${padding}px`,
        zIndex: 10002,
        maxWidth: `${tooltipWidth}px`
      };
    }

    if (!highlightPosition) {
      return {
        position: 'fixed',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        zIndex: 10002,
        maxWidth: `${tooltipWidth}px`
      };
    }

    const position = step.position || 'bottom';

    //   
    let left = 0;
    let top = 0;
    let transform = '';

    if (position === 'center') {
      // center   
      return {
        position: 'fixed',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        zIndex: 10002,
        maxWidth: `${tooltipWidth}px`
      };
    }

    switch (position) {
      case 'top':
        left = Math.max(padding + tooltipWidth / 2, Math.min(window.innerWidth - padding - tooltipWidth / 2, highlightPosition.left + highlightPosition.width / 2));
        top = Math.max(padding, highlightPosition.top - 20);
        transform = 'translate(-50%, -100%)';
        break;
      case 'bottom':
        left = Math.max(padding + tooltipWidth / 2, Math.min(window.innerWidth - padding - tooltipWidth / 2, highlightPosition.left + highlightPosition.width / 2));
        top = Math.min(window.innerHeight - padding, highlightPosition.bottom + 20);
        transform = 'translateX(-50%)';
        break;
      case 'bottom-right':
        //     (  )
        left = window.innerWidth - padding;
        top = window.innerHeight - padding;
        transform = 'translate(-100%, -100%)';
        break;
      case 'left':
        left = Math.max(padding, highlightPosition.left - 20);
        top = Math.max(padding, Math.min(window.innerHeight - padding, highlightPosition.top + highlightPosition.height / 2));
        transform = 'translate(-100%, -50%)';
        break;
      case 'right':
        left = Math.min(window.innerWidth - padding, highlightPosition.right + 20);
        //    150px  
        const topOffset = step.id === 'core-importance-filter' ? 150 : 0;
        top = Math.max(padding, Math.min(window.innerHeight - padding, highlightPosition.top + highlightPosition.height / 2 + topOffset));
        transform = 'translateY(-50%)';
        break;
      default:
        left = Math.max(padding + tooltipWidth / 2, Math.min(window.innerWidth - padding - tooltipWidth / 2, highlightPosition.left + highlightPosition.width / 2));
        top = Math.min(window.innerHeight - padding, highlightPosition.bottom + 20);
        transform = 'translateX(-50%)';
    }

    return {
      position: 'fixed',
      zIndex: 10002,
      maxWidth: `${tooltipWidth}px`,
      left: `${left}px`,
      top: `${top}px`,
      transform
    };
  };

  //    
  const getCursorPosition = () => {
    //       (  )
    if (step.subSteps && step.subSteps.length > 0) return null;

    if (!highlightPosition) return null;

    //       (   )
    if (isInteractiveStep) return null;

    // center position    
    if (step.position === 'center') return null;

    //        
    const rightPanelSteps = ['right-panel-intro', 'memo-title-tags', 'text-editing', 'file-attachment', 'connection-navigation'];
    if (rightPanelSteps.includes(step.id)) return null;

    //       
    const noCursorSteps = ['core-quick-nav-use', 'core-importance', 'core-importance-filter'];
    if (noCursorSteps.includes(step.id)) return null;

    //     
    return {
      x: highlightPosition.left + highlightPosition.width / 2,
      y: highlightPosition.top + highlightPosition.height / 2
    };
  };

  const cursorPosition = getCursorPosition();

  //    ( )
  const getArrowPath = () => {
    if (!highlightPosition || step.position === 'center') return null;

    //       
    if (isInteractiveStep) return null;

    //        
    const rightPanelSteps = ['right-panel-intro', 'memo-title-tags', 'text-editing', 'file-attachment', 'connection-navigation'];
    if (rightPanelSteps.includes(step.id)) return null;

    //       
    const noArrowSteps = ['core-quick-nav-use', 'core-importance', 'core-importance-filter'];
    if (noArrowSteps.includes(step.id)) return null;

    const tooltipStyle = getTooltipStyle();

    //   
    let tooltipX = 0;
    let tooltipY = 0;

    if (tooltipStyle.left && tooltipStyle.top) {
      tooltipX = parseFloat(tooltipStyle.left as string);
      tooltipY = parseFloat(tooltipStyle.top as string);
    }

    //  
    const targetX = highlightPosition.left + highlightPosition.width / 2;
    const targetY = highlightPosition.top + highlightPosition.height / 2;

    return {
      startX: tooltipX,
      startY: tooltipY,
      endX: targetX,
      endY: targetY
    };
  };

  const arrowPath = getArrowPath();

  //     
  const renderDescription = (description: string) => {
    // "1. ", "2. ", "3. "   
    const parts = description.split(/(\d+\.)/);
    let numberIndex = 0;

    return parts.map((part, index) => {
      if (/^\d+\.$/.test(part)) {
        // .  
        numberIndex++;
        return (
          <span key={index} className={`step-number step-number-${numberIndex}`}>
            {part}
          </span>
        );
      }
      return part;
    });
  };

  return (
    <>
      {/*   (   dim ,   ) */}
      {/*    dim   */}
      {!isCoreFeatureStep && highlightPosition ? (
        <>
          {isCanvasTarget ? (
            //    :   dim 
            <>
              {/*   dim */}
              <div
                style={{
                  position: 'fixed',
                  top: 0,
                  left: 0,
                  width: highlightPosition.left,
                  height: '100vh',
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  zIndex: 9999,
                  pointerEvents: 'none'
                }}
              />
              {/*   dim */}
              <div
                style={{
                  position: 'fixed',
                  top: 0,
                  left: highlightPosition.right,
                  width: window.innerWidth - highlightPosition.right,
                  height: '100vh',
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  zIndex: 9999,
                  pointerEvents: 'none'
                }}
              />
              {/*    */}
              <div
                className={styles['highlight-border']}
                style={{
                  top: highlightPosition.top - 4,
                  left: highlightPosition.left - 4,
                  width: highlightPosition.width + 8,
                  height: highlightPosition.height + 8,
                  pointerEvents: 'none'
                }}
              />
            </>
          ) : (
            //  :   dim 
            <>
              {/*  dim */}
              <div
                style={{
                  position: 'fixed',
                  top: 0,
                  left: 0,
                  width: '100vw',
                  height: highlightPosition.top,
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  zIndex: 9999,
                  pointerEvents: 'auto'
                }}
                onClick={(e) => e.stopPropagation()}
              />
              {/*  dim */}
              <div
                style={{
                  position: 'fixed',
                  top: highlightPosition.top,
                  left: 0,
                  width: highlightPosition.left,
                  height: highlightPosition.height,
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  zIndex: 9999,
                  pointerEvents: 'auto'
                }}
                onClick={(e) => e.stopPropagation()}
              />
              {/*  dim */}
              <div
                style={{
                  position: 'fixed',
                  top: highlightPosition.top,
                  left: highlightPosition.right,
                  width: window.innerWidth - highlightPosition.right,
                  height: highlightPosition.height,
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  zIndex: 9999,
                  pointerEvents: 'auto'
                }}
                onClick={(e) => e.stopPropagation()}
              />
              {/*  dim */}
              <div
                style={{
                  position: 'fixed',
                  top: highlightPosition.bottom,
                  left: 0,
                  width: '100vw',
                  height: window.innerHeight - highlightPosition.bottom,
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  zIndex: 9999,
                  pointerEvents: 'auto'
                }}
                onClick={(e) => e.stopPropagation()}
              />
              {/*   (  ,   z-index) */}
              <div
                className={styles['highlight-border']}
                style={{
                  top: highlightPosition.top - 4,
                  left: highlightPosition.left - 4,
                  width: highlightPosition.width + 8,
                  height: highlightPosition.height + 8,
                  pointerEvents: 'none'
                }}
              />
            </>
          )}

          {/*   */}
          {cursorPosition && !activeSubStep && (
            <div
              className={styles['tutorial-cursor']}
              style={{
                left: `${cursorPosition.x}px`,
                top: `${cursorPosition.y}px`
              }}
            >
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                <path
                  d="M3 3L10.07 19.97L12.58 12.58L19.97 10.07L3 3Z"
                  fill="#4A90E2"
                  stroke="white"
                  strokeWidth="2"
                />
              </svg>
            </div>
          )}

          {/*   */}
          {activeSubStep && subStepTargetPosition && (
            <>
              {/*    */}
              <div
                className={styles['substep-highlight']}
                style={{
                  top: subStepTargetPosition.top - 4,
                  left: subStepTargetPosition.left - 4,
                  width: subStepTargetPosition.width + 8,
                  height: subStepTargetPosition.height + 8,
                  pointerEvents: 'none'
                }}
              />

              {/*    */}
              {activeSubStep.animationType === 'cursor-click' && (
                <div
                  className={styles['substep-cursor-click']}
                  style={{
                    left: `${subStepTargetPosition.left + subStepTargetPosition.width / 2}px`,
                    top: `${subStepTargetPosition.top + subStepTargetPosition.height / 2}px`
                  }}
                >
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                    <path
                      d="M3 3L10.07 19.97L12.58 12.58L19.97 10.07L3 3Z"
                      fill="#FF6B6B"
                      stroke="white"
                      strokeWidth="2"
                    />
                  </svg>
                </div>
              )}

              {activeSubStep.animationType === 'cursor-drag' && (
                <div
                  className={styles['substep-cursor-drag']}
                  style={{
                    left: `${subStepTargetPosition.left + subStepTargetPosition.width / 2}px`,
                    top: `${subStepTargetPosition.top + subStepTargetPosition.height / 2}px`
                  }}
                >
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                    <path
                      d="M3 3L10.07 19.97L12.58 12.58L19.97 10.07L3 3Z"
                      fill="#FF6B6B"
                      stroke="white"
                      strokeWidth="2"
                    />
                  </svg>
                </div>
              )}

              {activeSubStep.animationType === 'cursor-hover' && (
                <div
                  className={styles['substep-cursor-hover']}
                  style={{
                    left: `${subStepTargetPosition.left + subStepTargetPosition.width / 2}px`,
                    top: `${subStepTargetPosition.top + subStepTargetPosition.height / 2}px`
                  }}
                >
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                    <path
                      d="M3 3L10.07 19.97L12.58 12.58L19.97 10.07L3 3Z"
                      fill="#FF6B6B"
                      stroke="white"
                      strokeWidth="2"
                    />
                  </svg>
                </div>
              )}
            </>
          )}

          {/*   ( ) */}
          {arrowPath && step.position !== 'center' && (
            <svg
              style={{
                position: 'fixed',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                zIndex: 10002,
                pointerEvents: 'none'
              }}
            >
              <defs>
                <marker
                  id="arrowhead"
                  markerWidth="10"
                  markerHeight="10"
                  refX="9"
                  refY="3"
                  orient="auto"
                >
                  <polygon points="0 0, 10 3, 0 6" fill="#4A90E2" />
                </marker>
              </defs>
              <path
                d={`M ${arrowPath.startX} ${arrowPath.startY} Q ${(arrowPath.startX + arrowPath.endX) / 2} ${(arrowPath.startY + arrowPath.endY) / 2 - 50}, ${arrowPath.endX} ${arrowPath.endY}`}
                stroke="#4A90E2"
                strokeWidth="3"
                fill="none"
                markerEnd="url(#arrowhead)"
                opacity="0.8"
                strokeDasharray="10,5"
                className={styles['arrow-path']}
              />
            </svg>
          )}
        </>
      ) : !isCoreFeatureStep ? (
        /*      (  ) */
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            width: '100vw',
            height: '100vh',
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            zIndex: 10000,
            pointerEvents: 'auto'
          }}
        />
      ) : null}

      {/*   */}
      <div style={getTooltipStyle()}>
        <div className={styles['tooltip-card']}>
          {/*   */}
          <div className={styles['tooltip-header']}>
            <span className={styles['step-counter']}>
              {currentStep + 1} / {steps.length}
            </span>
            <button
              onClick={onSkip}
              className={styles['skip-button']}
            >
              
            </button>
          </div>

          {/*   */}
          <div className={styles['progress-bar']}>
            <div
              className={styles['progress-fill']}
              style={{
                width: `${((currentStep + 1) / steps.length) * 100}%`
              }}
            />
          </div>

          {/*  */}
          <h3 className={styles['tooltip-title']}>
            {step.title}
          </h3>

          {/*  */}
          <p className={styles['tooltip-description']}>
            {renderDescription(step.description)}
          </p>

          {/*   */}
          <div className={styles['tooltip-actions']}>
            {/*   (     ) */}
            {currentStep > 0 && onPrev && (
              <button
                onClick={onPrev}
                className={`${styles['action-button']} ${styles.secondary}`}
              >
                
              </button>
            )}

            {step.id === 'basic-features-intro' && onSwitchToCore ? (
              //         ( )
              <>
                <button
                  onClick={onComplete}
                  className={`${styles['action-button']} ${styles.secondary}`}
                >
                   
                </button>
                <button
                  onClick={onSwitchToCore}
                  className={`${styles['action-button']} ${styles.primary}`}
                >
                  {step.nextButtonText || '  '}
                </button>
              </>
            ) : isLastStep ? (
              <button
                onClick={onComplete}
                className={`${styles['action-button']} ${styles.primary}`}
              >
                
              </button>
            ) : (
              <button
                onClick={canProceed ? onNext : undefined}
                disabled={!canProceed}
                className={`${styles['action-button']} ${styles.primary}`}
              >
                {step.nextButtonText || ''}
              </button>
            )}
          </div>
        </div>
      </div>

    </>
  );
};
